.button {
  --color: #fff;
  --background: #6D58FF;
  --height: 160px;
  --width: 160px;
  --icon: #fff;
  padding: 0;
  cursor: pointer;
  text-align: center;
  position: relative;
  border: none;
  outline: none;
  background: none;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;
  width: var(--width);
  letter-spacing: .5px;
  font-size: 14px;
  font-weight: 500;
  color: var(--color);
  line-height: var(--height);
  border-radius: 50%;
  font-family: 'Nunito', sans-serif;
}

.button:before,
.button:after {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
}

.button:before {
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: var(--width);
  width: var(--before-w, var(--width));
  height: var(--height);
  height: var(--before-h, var(--height));
  border-radius: 7px;
  border-radius: var(--before-r, 7px);
  background: var(--background);
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  -webkit-transition: width .3s, height .3s, border-radius .2s;
  transition: width .3s, height .3s, border-radius .2s;
  -webkit-transition-delay: .2s;
          transition-delay: .2s;
  border-radius: 50%;
}

.button:after {
  width: 16px;
  height: 16px;
  margin: -8px 0 0 -8px;
  background: var(--icon);
  z-index: 2;
  border-radius: 50%;
  -webkit-transform: scale(0);
  transform: scale(0);
}

.button span {
  z-index: 1;
  display: block;
  position: relative;
  opacity: 1;
  opacity: var(--span-o, 1);
  -webkit-transform: scale(1);
  -webkit-transform: scale(var(--span-s, 1));
  transform: scale(1);
  transform: scale(var(--span-s, 1));
  transition: opacity .3s, -webkit-transform .2s;
  -webkit-transition: opacity .3s, -webkit-transform .2s;
  transition: opacity .3s, transform .2s;
  transition: opacity .3s, transform .2s, -webkit-transform .2s;
  -webkit-transition-delay: .2s;
          transition-delay: .2s;
  border-radius: 50%;
}

.button svg {
  width: 15px;
  height: 13px;
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  stroke: var(--icon);
  stroke-width: 2px;
  stroke-linecap: round;
  stroke-linejoin: round;
  fill: none;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  stroke-dasharray: 17px;
  stroke-dashoffset: 17px;
  stroke-dashoffset: var(--svg-o, 17px);
  -webkit-transition: stroke-dashoffset .3s ease;
  transition: stroke-dashoffset .3s ease;
}

.button.processing {
  --before-w: 41px;
  --before-h: 41px;
  --before-r: 22px;
  --span-o: 0;
  --span-s: .6;
  --after-s: 1;
  --svg-o: 34px;
}

.button.processing:before {
  -webkit-animation: pulse 3s linear forwards;
  animation: pulse 3s linear forwards;
}

.button.processing:after {
  -webkit-animation: dot 3s linear forwards;
  animation: dot 3s linear forwards;
}

.button.processing svg {
  -webkit-transition-delay: 3s;
          transition-delay: 3s;
}

.element-move-top {
  -webkit-animation: movetop 1s linear forwards;
          animation: movetop 1s linear forwards;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}

@-webkit-keyframes movetop {
  0% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
  }

  100% {
    -webkit-transform: translate(0px, -150px);
            transform: translate(0px, -150px);
  }
}

@keyframes movetop {
  0% {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px);
  }

  100% {
    -webkit-transform: translate(0px, -150px);
            transform: translate(0px, -150px);
  }
}

@-webkit-keyframes pulse {
  4% {
    -webkit-transform: translate(-50%, -50%) scaleX(1.1);
    transform: translate(-50%, -50%) scaleX(1.1);
  }

  8% {
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }

  24%,
  56%,
  88% {
    -webkit-transform: translate(-50%, -50%) scale(0.8);
    transform: translate(-50%, -50%) scale(0.8);
  }

  40%,
  72% {
    -webkit-transform: translate(-50%, -50%) scale(1.2);
    transform: translate(-50%, -50%) scale(1.2);
  }
}

@keyframes pulse {
  4% {
    -webkit-transform: translate(-50%, -50%) scaleX(1.1);
    transform: translate(-50%, -50%) scaleX(1.1);
  }

  8% {
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }

  24%,
  56%,
  88% {
    -webkit-transform: translate(-50%, -50%) scale(0.8);
    transform: translate(-50%, -50%) scale(0.8);
  }

  40%,
  72% {
    -webkit-transform: translate(-50%, -50%) scale(1.2);
    transform: translate(-50%, -50%) scale(1.2);
  }
}

@-webkit-keyframes dot {
  8% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }

  16% {
    -webkit-transform: scale(1) rotate(30deg) translateX(48px);
    transform: scale(1) rotate(30deg) translateX(48px);
  }

  95% {
    -webkit-transform: scale(1) rotate(1000deg) translateX(48px);
    transform: scale(1) rotate(1000deg) translateX(48px);
  }

  100% {
    -webkit-transform: scale(0.125) rotate(1050deg) translate(52px, -6px);
    transform: scale(0.125) rotate(1050deg) translate(52px, -6px);
  }
}

@keyframes dot {
  8% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }

  16% {
    -webkit-transform: scale(1) rotate(30deg) translateX(48px);
    transform: scale(1) rotate(30deg) translateX(48px);
  }

  95% {
    -webkit-transform: scale(1) rotate(1000deg) translateX(48px);
    transform: scale(1) rotate(1000deg) translateX(48px);
  }

  100% {
    -webkit-transform: scale(0.125) rotate(1050deg) translate(52px, -6px);
    transform: scale(0.125) rotate(1050deg) translate(52px, -6px);
  }
}
